body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: "IBMPlexMono";
  src: local("IBMPlexMono"),
    url(/static/media/IBMPlexMono-Bold.8d845e99.ttf) format("truetype");
    font-weight: bold;
}
@font-face {
  font-family: "IBMPlexMono";
  src: local("IBMPlexMono"),
    url(/static/media/IBMPlexMono-Regular.ad8e1092.ttf) format("truetype");
    font-weight: normal;
}
@font-face {
  font-family: "enhancedDot";
  src: local("enhancedDot"),
    url(/static/media/enhanced_dot.1d2ca778.ttf) format("truetype");
    font-weight: normal;
}

*{
  font-family: "IBMPlexMono";
  box-sizing: border-box;
}
.text-dot{
  font-family: "enhancedDot";
}
h1, h2, h3, h4{
  color: #1E2852;
}
nav{
  width: 100%;
  height:80px;
  display:flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  box-sizing: border-box;
  background-color: orangered;
}
nav h1{
  color: white;
}
nav img{
  width: 50px;
  height: 50px;
}
.orange{
  color: orangered;
}
.tj{
  text-align: justify;
}
.big-text{
  font-size: 4rem;
}

.App {
  display: flex;
  width: 100%;
  height: 100vh;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.in,
.btn {
    height: 45px;
    width: 100px;
    border-radius: 4px;
    outline: none;
    padding: 4px;
    box-sizing: border-box;
}
.in{
    border: 2px solid #000;
    font-size: 28px;
    text-align: center;
}
.btn-color {
  background-color: #128f5f;
  color: #fff;
  height: 45px;
  width: 100px;
  border-radius: 4px;
  display: flex;
  justify-content: center;
  align-items: center;
  -webkit-user-select: none;
          user-select: none;
}
.center{
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.column{
  flex-direction: column;
}
.space{
  width: 100%;
  height: 100px;
}
.min-space{
  width: 100%;
  height: 50px;
}
.grid1{
  display: grid;
  grid-template-columns: 1fr;
  padding: 5px;
  box-sizing: border-box;
}
.grid2{
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 5px;
  box-sizing: border-box;
}
.h-100{
  height: 100vh;
}
.h-100p{
  height: 100%;
}
.h-50{
  height: 50vh;
}
.h-40{
  height: 40vh;
}
.p-20{
  padding: 20px;
}
.w-50{
  width: 50%;
}
.w-100{
  width: 100%;
}
.d-grid{
  display:grid;
  grid-gap:10px;
  gap:10px;
  padding: 10px;
}
.col-auto{
  grid-template-columns: repeat(auto-fit,minmax(400px,auto));
}

/* responsive */

@media only screen and (max-width: 600px){
  nav h1{
    font-size: 1.5rem !important;
  }
  .w-50{
    width: 100%;
    margin: 10px;
  }
  .d-grid{
    grid-gap: 60px;
    gap: 60px;
  }
  .min-space-visible-mobile{
    height: 50px;
    width: 100%;
  }
  .col-auto{
    grid-template-columns: repeat(auto-fit,minmax(300px,auto));
  }
}

.image-box {
    min-height: 150px;
    min-width: 300px;
    display: grid;
    grid-template-columns: 1fr;
    padding: 10px;
    box-sizing: border-box;
    border-radius: 4px;
}

.image-box-row {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    color: #000;
}


#color {
    display: none;
}

@media only screen and (max-width: 400px) {
    .image-box-row-input-row {
        flex-direction: column;
        height: 150px;
    }
    .invisible-mobile {
        display: none;
    }
}
.video-box {
    min-height: 150px;
    min-width: 50%;
    display: grid;
    grid-template-columns: 1fr;
    padding: 10px;
    box-sizing: border-box;
    border-radius: 4px;
}
.video-box-row {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    color: #000;
    grid-gap: 10px;
    gap: 10px;
}

.content-box {
    min-width: 350px;
    min-height: 400px;
    background-color: #fff;
    border: 2px solid #ccc;
    border-radius: 8px;
    transition: 0.4s;
}

.content-box:hover {
    background-color: rgb(241, 241, 241);
    box-shadow: 5px 5px#ccc;
    margin-top: -10px;
    transition: 0.4s;
}

.content-box-border-top {
    height: 10px;
    background-color: #fff;
    width: 100%;
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
    transition: 0.4s;
    z-index: 1;
}

.constent-box-icon {
    width: 100px;
    height: 100px;
    margin-left: calc(50% - 50px);
    display: flex;
    justify-content: center;
    background-color: #fff;
    border: 10px solid #ccc;
    align-items: center;
    border-radius: 50%;
    margin-top: -60px;
    z-index: 2;
}

.constent-box-icon>h1 {
    -webkit-user-select: none;
            user-select: none;
}

.content-box-title {
    width: 100%;
    text-align: center;
}

.content-box-container {

    width: 100%;
    min-height: 300px;
}
